<template>
  <div class="room">
      <!--
      {{$route.params}}
      {{MN_NO}}
      -->
      <div style="background-color:#82FF82;">修改頁面</div>
      <div>監測項目：<input type="text" id="MN_NAM_EDIT_Btn" v-model="MN_NAM" /></div>
      <div>檢體來源：
          <select id="SRC" v-model="SRC">
            <option value="1">水質</option>
            <option value="2">器械</option>
            <option value="3">環境</option>
            <option value="4">人員</option>
          </select>
        </div>
      <div>狀態：
          <select id="TSC" v-model="TSC">
            <option value="A">生效</option>
            <option value="D">失效</option>
          </select>
      </div>
      <div style="text-align:center">
        <input type="button" id="editBtn" value="儲存" @click="updData()"/>
      </div>
  </div>
</template>
<script>
import { Base64 } from 'js-base64'
export default {
  name: 'room',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      MN_NO:this.$route.params.MN_NO,
      MN_COD:this.$route.params.MN_COD,
      MN_NAM:this.$route.params.MN_NAM,
      SRC:this.$route.params.SRC,
      EFF_DAT:this.$route.params.EFF_DAT,
      STP_DAT:this.$route.params.STP_DAT,
      TSC:this.$route.params.TSC,
      RTP:this.$route.params.RTP,
      RTT:this.$route.params.RTT,
      resultReturn:''
    }
  },
    methods : {
        updData() {
        let tag = 'pvt.if.trnMnCodData'
        let param = new FormData()
        let conf = {
          params : {
            wtag: tag
          }
        }
        let obj = {
          wb_base64 :0,
          encode : "N",
          funTyp : 'U',
          MN_NO : this.MN_NO,
          MN_NAM :this.MN_NAM,
          SRC : this.SRC,
          TSC : this.TSC,
          RTP : this.RTP
        }
        param.append("var", Base64.encode(JSON.stringify(obj)));
        this.$axios.post("/api", param, conf).
        then(Response => {
          let result = JSON.parse(Base64.decode(Response.data))
          console.log(result)
          if (result.sts = '000000') {
            console.log('呼叫ws成功了!')
            this.$router.push({
              name: 'HelloWorld',
              params: { MN_COD:this.MN_COD }
            })
          } else {
            console.log(result.sts, result.val)
          }
        }).catch(error => {
          console.log(error)
        })
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
#editBtn {
  width:80px;
  height:30px;
  border:0;
  background-color:#003C9D;
  color:#fff;
  border-radius:10px;
  cursor:pointer;
  font-family:Microsoft JhengHei;
  font-size:16px;
}

#editBtn:hover {
  color:#003C9D;
  background-color:#fff;
  border:2px #003C9D solid;
}
</style>



