<template>
  <div class="bottomStyle">
    <div>功能：<router-link :to="{ name: 'contact', params: '' }"  style="font-weight:bold;" class="blink">新增</router-link></div>
    <div>監測項目：
      <select id="MN_COD" v-model="MN_COD">
         <option v-for="(item, index) in mrCodArr" :value=item.MN_COD>{{item.MN_NAM}}</option>
      </select>
    </div>
    <div>
      狀態：
      <select id="TSC" v-model="TSC">
         <option value="A">生效</option>
         <option value="D">失效</option>
      </select>
    </div>
    <div style="text-align:center">
      <input type="button" id="serachBtn" value="查詢" @click="getDrugBasicData()"/>
    </div>
    <div align="center" id="Grid" style="display:none">
        <table border="0">
          <tr>
            <td>
              <table border="1" style="font-size:18px;">
                <tr id="titleBar" style="display:none">
                  <td v-for="(itmTitle, idxTitle) in titleArr">
                    {{itmTitle.title}}
                  </td>  
                </tr>
                <tr v-for="(itm, idx) in resultReturn">
                  <td>
                    <!--<input type="button" id="editBtn" value="修修改改" @click="editData(resultReturn[idx]['MN_NO'])"/>-->
                    <router-link :to="{ name: 'room', params: itm }"  style="font-weight:bold;color:#CC0000;">編輯</router-link>
                  </td>
                  <td v-for="(itmTd, idxTd) in itm">
                    <span v-if="idxTd == 'TSC'">
                      <span v-if="itmTd == 'A'">生效</span>
                      <span v-else>失效</span>
                    </span>
                    <span v-else>
                      <span v-if="idxTd == 'SRC'">
                        <span v-if="itmTd == '1'">水質</span>
                        <span v-else-if="itmTd == '2'">器械</span>
                        <span v-else-if="itmTd == '3'">環境</span>
                        <span v-else>人員</span>
                      </span>
                      <span v-else>{{itmTd}}</span>
                    </span>
                  </td>  
                </tr>
              </table>
            </td>
          </tr>
        </table>
    </div>
  </div>
</template>
<script>
import { Base64 } from 'js-base64'
export default {
  name: 'HelloWorld',
   data() {
      return {
        message:'hello',
        dollors:'',
        TSC:'A',
        MN_COD:'',
        resultReturn:'',
        titleArr:[
          {
title: '#'
},{
title: '監測序號'
},{
title: '監測編碼'
},{
title: '監測項目'
},{
title: '檢體來源'
},{
title: '生效日期'
},{
title: '失效日期'
},{
title: '狀態'
},{
title: '異動人員'
},{
title: '異動日期'
}],
        mrCodArr:[{
MN_NO: '20190128084743000021',
MN_COD: 'AIRB',
MN_NAM: '空氣落菌',
SRC: '1'
},
{
MN_NO: '20190201135955000023',
MN_COD: 'BCWQ',
MN_NAM: '燒傷中心水質',
SRC: '1'

},
{
MN_NO: '20190308134623000026',
MN_COD: 'ERPA',
MN_NAM: '員工餐廳準備區',
SRC: '3'

},
{
MN_NO: '20190123143114000020',
MN_COD: 'ERTB',
MN_NAM: '員工餐廳餐具',
SRC: '2'
},
{
MN_NO: '20190306110518000025',
MN_COD: 'ERWQ',
MN_NAM: '員工餐廳水質',
SRC: '1'
},
{
MN_NO: '20190115144044000004',
MN_COD: 'ESMN',
MN_NAM: '內視鏡監測',
SRC: '2'
},
{
MN_NO: '20190314100148000029',
MN_COD: 'ESMN-G',
MN_NAM: '內視鏡監測(腸胃管鏡)',
SRC: '1',
}]
      }
    },
    mounted () {
      //this.getDrugBasicData();
        // this.$axios.get("/api").then(result=>{
        //             console.log('success=>',result)
        //             this.dollors = result.data;
        //           }).catch(error=>{
        //             console.log('not success=>',error)
        //           })
        console.log(this.$route.params);
      if (this.$route.params.MN_COD) {
          this.MN_COD = this.$route.params.MN_COD;
          this.getDrugBasicData();
      }
    },
    methods : {
      getDrugBasicData() {
        let tag = 'pvt.if.getMnCodData'
        let param = new FormData()
        let conf = {
          params : {
            wtag: tag
          }
        }
        let obj = {
          wb_base64 :0,
          encode : "N",
          MN_COD : this.MN_COD,
          TSC : this.TSC
        }
        param.append("var", Base64.encode(JSON.stringify(obj)));
        this.$axios.post("/api", param, conf).
        then(Response => {
          let result = JSON.parse(Base64.decode(Response.data))
          //console.log(result)
          var x;
          for (var i=0;i<result.val.length;i++)
          { 
              for(x in result.val[i]) {
                  //console.log(x)
                  if (x == 'MN_NAM') {
                      result.val[i][x] = Base64.decode(result.val[i][x]);
                  } else if (x == 'uiNam' || x == 'tscNam' || x == 'srcNam' || x == 'TAG' || x == 'dfDesc' || x == 'dfCmt') {
                      delete result.val[i][x];
                  }
              }
          }
          console.log(result)
          if (result.sts = '000000') {
            console.log('呼叫ws成功了!')
            document.getElementById("titleBar").style.display = "";
            document.getElementById("Grid").style.display = "";
            this.resultReturn = result.val;
            
          } else {
            console.log(result.sts, result.val)
          }
        }).catch(error => {
          console.log(error)
        })
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!-- 閃爍用法：https://www.wibibi.com/info.php?tid=CSS3_animation_%E5%8B%95%E7%95%AB%E5%B1%AC%E6%80%A7-->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.bottomStyle {
  animation: yellow-orange 5s infinite;
  text-align:left;
  width:auto;
  height:100vh;
}
@keyframes yellow-orange {
    0% {
        background: #CCDDFF;
    }
    50% {
        background: #FFFF77;
    }
    100% {
        background: #CCDDFF;
    }
}
.blink {
    animation-duration: 1s;
    animation-name: blink;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: ease-in-out;
    background:#FF5511;
    color:#444444;
    font-size:18px;
}
@keyframes blink {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}
#serachBtn {
  width:80px;
  height:30px;
  border:0;
  background-color:#003C9D;
  color:#fff;
  border-radius:10px;
  cursor:pointer;
  font-family:Microsoft JhengHei;
  font-size:16px;
}

#serachBtn:hover {
  color:#003C9D;
  background-color:#fff;
  border:2px #003C9D solid;
}
</style>



