<template>
  <div class="contact">
      <div style="background-color:#82FF82;">新增頁面</div>
      <div>監測編碼：<input type="text" id="MN_COD_EDIT_Btn" v-model="MN_COD" /></div>
      <div>監測項目：<input type="text" id="MN_NAM_EDIT_Btn" v-model="MN_NAM" /></div>
      <div>檢體來源：
          <select id="SRC" v-model="SRC">
            <option value="1">水質</option>
            <option value="2">器械</option>
            <option value="3">環境</option>
            <option value="4">人員</option>
          </select>
      </div>
      <div style="text-align:center">
        <input type="button" id="addBtn" value="儲存" @click="addData()"/>
      </div>
  </div>
</template>
<script>
import { Base64 } from 'js-base64'
export default {
  name: 'contact',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      MN_COD:'',
      MN_NAM:'',
      SRC:'1'
    }
  },
    methods : {
      addData() {
        let tag = 'pvt.if.trnMnCodData'
        let param = new FormData()
        let conf = {
          params : {
            wtag: tag
          }
        }
        var date = new Date();
        var year = date.getFullYear();
        var month = date.getMonth() + 1;
        var day = date.getDate();
        var hh = date.getHours();
        var mm = date.getMinutes();
        var ss = date.getSeconds()
        if (month < 10) {
            month = "0" + month;
        }
        if (day < 10) {
            day = "0" + day;
        }
        if (hh < 10) {
            hh = "0" + hh;
        }
        if (mm < 10) {
            mm = "0" + mm;
        }
        if (ss < 10) {
            ss = "0" + ss;
        }
        var nowDate = year + "-" + month + "-" + day;
        var nowDateLong = year + "-" + month + "-" + day+ " " + hh + ":" + mm + ":" + ss;
        let obj = {
          wb_base64 :0,
          encode : "N",
          funTyp : 'A',
          MN_COD : this.MN_COD,
          MN_NAM : this.MN_NAM,
          SRC    : this.SRC,
          RTP    : '12833'
        }
        console.log(obj)
        param.append("var", Base64.encode(JSON.stringify(obj)));
        this.$axios.post("/api", param, conf).
        then(Response => {
          let result = JSON.parse(Base64.decode(Response.data))
          //console.log(result)
          console.log(result)
          if (result.sts = '000000') {
            console.log('呼叫ws成功了!')
            this.$router.push({
              name: 'HelloWorld',
              params: { MN_COD:this.MN_COD }
            })
          } else {
            console.log(result.sts, result.val)
          }
        }).catch(error => {
          console.log(error)
        })
      },
      padLeft(str,lenght) {
        if(str.length >= lenght){
            return str;
        } else{
            return padLeft("0" +str,lenght);
        }
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
#addBtn {
  width:80px;
  height:30px;
  border:0;
  background-color:#003C9D;
  color:#fff;
  border-radius:10px;
  cursor:pointer;
  font-family:Microsoft JhengHei;
  font-size:16px;
}

#addBtn:hover {
  color:#003C9D;
  background-color:#fff;
  border:2px #003C9D solid;
}
</style>



