      doFoodGogo(name, time ,success) {
        return new Promise((resolve, reject) =>{
          if (success) {
              setTimeout(() => {
                resolve(`${name} 花了 ${time/1000} 秒完成送餐`)
              },time)
          } else {
            reject(`${name}掉到海裡了`)
